<template>
    <article class="service" v-if="svc">
        <ul class="links">
            <li v-if="svc.link.home"><a :href="svc.link.home" title="Gå til tjenesten"><fa-icon name="external-link"/></a></li>
            <li v-if="svc.link.doc"><a :href="svc.link.doc" title="Dokumentasjon om tjenesten"><fa-icon name="question-circle"/></a></li>
            <li v-if="bus.ita_mode"><a :href="svc.link.href_tk + '/edit'" title="Dokumentasjon om tjenesten"><fa-icon name="pencil-square-o"/></a></li>
        </ul>
        <div class="kicker"><fa-icon name='cog'/> Tjeneste</div>
        <h1>{{svc.title}}</h1>
        <p v-if="'summary' in svc.text" class="lead">{{svc.text.summary}}</p>
        <p v-if="'description' in svc.text" class="desc">{{svc.text.description}}</p>
        <p v-if="bus.ita_mode">State: {{svc.info.service_state}}</p>
    </article>
</template>

<script>
import FaIcon from '@/components/FaIcon.vue';
import { eventBus } from '@/main';
import tkdata from '@/tkdata';

export default {
    name: 'Service',
    props: {
        svcId: { type: String, required: true }
    },
    data() {
        return {
            svc: null,
            bus: eventBus,
        }
    },
    created() {
        tkdata().then(data => {
            this.svc = data.service[this.svcId];
        });
    },
    components: {
        FaIcon
    }
}
</script>

